import React, { useContext, useState } from 'react'
import StorylineContext from '@store/whatsapp-lebanon/storyline-context'
import IntroAnimation from '@components/scrollytelling/whatsapp-lebanon/intro-animation'
import { GatsbyImage } from 'gatsby-plugin-image'
import { motion } from 'framer-motion'
import CHARACTERS from '@data/whatsapp-lebanon/character-data'
import RichText from '@components/scrollytelling/whatsapp-lebanon/rich-text'
import CentredTextContainer from '@components/scrollytelling/whatsapp-lebanon/centred-text-container'
import { isMobile } from 'react-device-detect'

const ChapterZero = ({ getImage }) => {
	const { currentDir, timelineIsActive, currentLangData, setCurrentIntroChapter } = useContext(StorylineContext)

	return (
		<motion.div initial={{ opacity: 1 }} animate={{ opacity: timelineIsActive ? 0 : 1 }} exit={{ opacity: 0 }}>
			<IntroAnimation getImage={getImage} />
			<div className={'absolute top-0 left-0 w-full h-full flex justify-center items-center flex-col'} dir={currentDir}>
				<h1
					className={'mb-6 z-50 px-3 py-2 inline-block rounded mx-auto text-white sm:text-6xl md:text-7xl lg:text-8xl comic-font'}
					style={{
						textShadow: '0px 3px 15px rgba(0,0,0,0.8)',
					}}
				>
					{currentLangData?.app.storyTitle}
				</h1>
				<p
					className={'z-50 text-white sm:text-4xl md:text-5xl lg:text5xl xl:text-6xl'}
					style={{
						textShadow: '0px 3px 15px rgba(0,0,0,0.8)',
					}}
				>
					{currentLangData?.app.storyLead}
				</p>
				<div className={'flex gap-10 mt-4 z-50'}>
					<button
						className={'px-5 sm:px-10 py-2 sm:py-5 font-bold text-3xl sm:text-4xl mt-8 shadow-2xl comic-font button-outline z-50 pointer-events-auto'}
						onClick={isMobile ? null : () => setCurrentIntroChapter(1)}
						onTouchStart={
							!isMobile
								? null
								: () => {
									console.log('touch start')
									setCurrentIntroChapter(1)
								}
						}
						style={{
							background: '#e3be7e',
							color: '#000000',
						}}
					>
						{currentLangData?.app.nextButton}
					</button>
				</div>
			</div>
		</motion.div>
	)
}

const ChapterOne = () => {
	const { currentDir, setCurrentIntroChapter, lang, currentLangData } = useContext(StorylineContext)

	return (
		<>
			<CentredTextContainer>
				<div dir={currentDir} style={{ maxWidth: '1024px' }}>
					{lang === 'ar-LB' ? (
						<div className={'flow'}>
							<h2>
								                     
								 .
							</h2>
							<p>
								 /                 /   
								  .
							</p>
							<p>
								      /        /   .    
								    .
							</p>
							<p>
								                    
								  .                        
								         .
							</p>
							<p>
								    /               .    
								 .             .
							</p>
							<p>                    .</p>
							<p>
								                    .    
								             .
							</p>
							<div className={'p-5 flow'} style={{ background: '#FDE68A' }}>
								<h3>  </h3>
								<p>
									                       
									           /         / "
									 ".
								</p>
								<p>
									                      .
									                   
									      .               
									  .
								</p>
							</div>
							<p>     /    .</p>
							<p dir={'rtl'}>
								               /   )
								<ExternalLink href='https://www.thenewhumanitarian.org/ar/2022/07/28/qy-m-l-gny-wtsb-lbnn' target='_blank' rel='noopener noreferrer'>
									
								</ExternalLink>{' '}
								    /(   .
							</p>
							<p>     /      /</p>
							<p>
								/            .        
								   -         )/(.
							</p>
						</div>
					) : (
						<div className={'flow'}>
							<h2>
								This is the story of Afaf, Bassel, Mohamad, Roger, and Roza, told through the WhatsApp conversations theyve had with their friends, family, and
								colleagues over the past few years.
							</h2>
							<p>
								During that time, their lives have been shaped by the tumult they and the other 7 million residents of Lebanon have been forced to endure as
								they navigated the countrys massive economic collapse.
							</p>
							<p>
								This is what life over the past three years looked like for five young people in Lebanon, as they WhatsApped it. This timeline is their story,
								and its Lebanons too.{' '}
							</p>
							<p>
								Since late 2019, when mass protests against the government and a flagging economy left many in Lebanon with real hope for the first time in
								decades, the country has been in a downward spiral: The Lebanese lira has lost 90 percent of its value; nearly 90 percent of the population now
								lives beneath the poverty line; and shortages of fuel and electricity have become so dire that even hospitals have been on the verge of closure.
							</p>
							<p>
								Two years ago, on 4 August 2020, thousands of tons of ammonium nitrate exploded at Beiruts port and destroyed parts of the city. Recovery has
								been slow. People who can leave the country are leaving as fast as they can, joining an exodus of tens of thousands.
							</p>
							<p>
								Some of these events and numbers have made the news, but this timeline  and this story  is about the people behind those headlines and
								statistics.
							</p>
							<p>
								Sometimes they WhatsApped as the country where they live shifted in an instant, like during the Beirut blast, and sometimes they sent voice
								notes late at night, wondering when the electricity would come back on.
							</p>
							<div className={'p-5 flow shadow-2xl'} style={{ background: '#FDE68A', margin: '3rem auto' }}>
								<h3>Why WhatsApp?</h3>
								<p>
									One of the final straws that pushed people to the streets for the first time on 17 October 2019, which many in Lebanon have come to call the
									October 17 Revolution, was a government proposal to tax WhatsApp calls in an effort to raise revenue for a state with no reserves.
								</p>
								<p>
									People in Lebanon, like many places, use WhatsApp for pretty much everything: for daily communication, for GIFs, to send cute pictures. As the
									protests in Lebanon grew, and sometimes turned into clashes with security forces, they also used it to follow groups that disseminated local
									news or showed which roads were closed off by burning tyres. Eventually, WhatsApp became a way to circulate requests for help when medication
									or baby formula wasnt in the shops.
								</p>
							</div>
							<p>Youll see these sorts of messages as you scroll through the timeline.</p>
							<p>
								Youll also see that Afaf, Bassel, Mohamad, Roger, and Roza are Lebanese, Syrian, Palestinian, and Ethiopian. They have diverse musical tastes
								(click <ExternalLink href='https://www.thenewhumanitarian.org/2022/07/28/playlist-whatsapp-lebanon'>here</ExternalLink> to listen to a playlist
								of their picks), hobbies, and jobs.
							</p>
							<p>But as you move through time with them, youll also see that they have a lot in common.</p>
							<p>
								They all reached out to connect as their lives shifted in small and drastic ways. They sent messages to discuss the ballooning exchange rates,
								to share COVID-19 diagnoses, and to talk about their futures as young people in a country that was transforming before their eyes (and screens).
							</p>
						</div>
					)}
				</div>
			</CentredTextContainer>
			<div className={`flex gap-10 justify-center items-center ${lang === 'ar-LB' ? 'flex-row-reverse' : ''}`}>
				<motion.button
					className={'px-5 sm:px-10 py-2 sm:py-5 font-bold text-3xl sm:text-4xl mt-8 shadow-2xl comic-font button-outline'}
					initial={{ borderRadius: 0, background: '#ffffff', color: '#000000' }}
					animate={{ borderRadius: 0, background: '#ffffff', color: '#000000' }}
					onClick={() => {
						setCurrentIntroChapter(0)
					}}
				>
					{currentLangData?.app.prevButton}
				</motion.button>
				<motion.button
					className={'px-5 sm:px-10 py-2 sm:py-5 font-bold text-3xl sm:text-4xl mt-8 shadow-2xl comic-font button-outline'}
					initial={{ background: '#e3be7e', color: '#000000' }}
					animate={{ background: '#e3be7e', color: '#000000' }}
					onClick={() => setCurrentIntroChapter(2)}
				>
					{currentLangData?.app.nextButton}
				</motion.button>
			</div>
		</>
	)
}

const ChapterTwo = ({ getImage }) => {
	const { currentDir, activateTimeline, lang, currentLangData, setCurrentIntroChapter } = useContext(StorylineContext)
	const [currentlyOpen, setCurrentlyOpen] = useState(-1)

	const Portrait = ({ data, i, clickHandler, currentlyOpen }) => {
		return (
			<motion.div
				initial={{ y: 0, opacity: 1 }}
				animate={{ y: currentlyOpen === i ? -10 : 0, opacity: 1 }}
				transition={{ delay: i * 0.05 }}
				key={`character-container-${i}`}
				onClick={() => clickHandler(i)}
			>
				<motion.div whileHover={{ y: -10 }} className={'relative flex items-center justify-center flex-col gap-5 cursor-pointer'}>
					<div className={'flex justify-center items-center w-full'}>
						<GatsbyImage alt={'Character illustration'} image={getImage(`character-${data.id}`)} />
					</div>
					<p className={`text-black ${lang === 'ar-LB' ? 'font-bold' : 'comic-font font-inherit'}`}>{data.name[lang]}</p>
				</motion.div>
			</motion.div>
		)
	}

	return (
		<div
			className={`mx-5 sm:mx-20 p-5 sm:p-10 shadow-2xl flex justify-center items-center flex-col bg-white z-50 hand-drawn-el`}
			dir={currentDir}
			style={{ maxWidth: '1280px' }}
		>
			{currentlyOpen !== -1 ? (
				<div className={'flex sm:grid grid-cols-2 items-center justify-center place-items-center'}>
					<div className={'hidden sm:flex sm:w-1/2 sm:items-center'}>
						<GatsbyImage alt={'Character illustration'} image={getImage(`character-${currentlyOpen + 1}`)} />
					</div>
					<div className={`relative text-left flow flex flex-col items-start justify-start ${lang === 'ar-LB' ? 'text-right' : ''}`} dir={currentDir}>
						<h2 className={`${lang === 'ar-LB' ? '' : 'comic-font'}`}>
							<strong>
								{currentLangData?.app.about} {CHARACTERS[currentlyOpen + 1].name[lang]}
							</strong>
						</h2>
						{/* {CHARACTERS[currentlyOpen + 1].description && parse(CHARACTERS[currentlyOpen + 1].description[lang])} */}
						{CHARACTERS[currentlyOpen + 1].description && <RichText className={'flow'} text={CHARACTERS[currentlyOpen + 1].description[lang]} />}
					</div>
				</div>
			) : (
				<div className={'relative max-w-7xl text-left flex flex-col items-start justify-start'}>
					<h2>{currentLangData?.app.meetTheWhatsAppers}</h2>
					<div className={'grid grid-cols-5 pb-10 gap-8 pt-14'}>
						{CHARACTERS.filter((el) => el.id !== 0).map((el, i) => {
							return <Portrait i={i} key={`avatar-${i}`} data={el} currentlyOpen={currentlyOpen} clickHandler={setCurrentlyOpen} />
						})}
					</div>
				</div>
			)}

			{currentlyOpen === -1 ? (
				<div className={'flex gap-10'}>
					<div className={'flex gap-10'}>
						<motion.button
							className={'px-5 sm:px-10 py-2 sm:py-5 font-bold text-3xl sm:text-4xl mt-8 shadow-2xl comic-font button-outline'}
							initial={{ borderRadius: 0, background: '#FFF', color: '#000' }}
							animate={{ borderRadius: 0, background: '#FFF', color: '#000' }}
							onClick={() => {
								setCurrentIntroChapter(1)
							}}
						>
							{currentLangData?.app.prevButton}
						</motion.button>
					</div>
					<motion.button
						className={'px-5 sm:px-10 py-2 sm:py-5 font-bold text-3xl sm:text-4xl mt-8 shadow-2xl comic-font button-outline'}
						initial={{ background: '#e3be7e', color: '#000000' }}
						animate={{ background: '#e3be7e', color: '#000000' }}
						onClick={() => {
							activateTimeline()
						}}
					>
						{currentLangData?.app.nextButton}
					</motion.button>
				</div>
			) : (
				<div className={'flex flex-row gap-10'}>
					<div className={'flex gap-10'}>
						<motion.button
							className={'px-5 sm:px-10 py-2 sm:py-5 font-bold text-3xl sm:text-4xl mt-8 shadow-2xl comic-font button-outline'}
							initial={{ borderRadius: 0 }}
							animate={{ borderRadius: 0, background: '#FFF', color: '#000' }}
							onClick={() => {
								setCurrentlyOpen(-1)
							}}
						>
							{currentLangData?.app.prevButton}
						</motion.button>
					</div>
				</div>
			)}
		</div>
	)
}

const ExternalLink = ({ children, href }) => {
	return (
		<a href={href} target={'_blank'} className={'credits-link font-bold'}>
			{children}
		</a>
	)
}

