const IntroChapters = ({ getImage }) => {
	const { currentIntroChapter, timelineIsActive } = useContext(StorylineContext)

	const currentSlide = () => {
		switch (currentIntroChapter) {
			case 0:
				return <ChapterZero getImage={getImage} />
			case 1:
				return (
					<motion.div initial={{ opacity: 1 }} animate={{ opacity: timelineIsActive ? 0 : 1 }} exit={{ opacity: 0 }}>
						<div className={'absolute top-0 left-0 w-full h-full -z-1'}>
							<GatsbyImage alt={'WhatsApp background pattern'} image={getImage('whatsapp-pattern')} className={'absolute top-0 left-0 w-full h-full'} />
						</div>
						<ChapterOne />
					</motion.div>
				)
			case 2:
				return (
					<motion.div initial={{ opacity: 1 }} animate={{ opacity: timelineIsActive ? 0 : 1 }} exit={{ opacity: 0 }}>
						<div className={'absolute top-0 left-0 w-full h-full -z-1'}>
							<GatsbyImage alt={'WhatsApp background pattern'} image={getImage('whatsapp-pattern')} className={'absolute top-0 left-0 w-full h-full'} />
						</div>
						<ChapterTwo getImage={getImage} />
					</motion.div>
				)
			case 3:
				return (
					<motion.div initial={{ opacity: 1 }} animate={{ opacity: timelineIsActive ? 0 : 1 }} exit={{ opacity: 0 }}>
						<div className={'absolute top-0 left-0 w-full h-full -z-1'}>
							<GatsbyImage alt={'WhatsApp background pattern'} image={getImage('whatsapp-pattern')} className={'absolute top-0 left-0 w-full h-full'} />
						</div>
						<FinalChapter />
					</motion.div>
				)

			default:
				return <ChapterZero />
		}
	}

	return currentSlide()
}

export default IntroChapters
